import { saveToStorage } from './login';
const loginBlock = document.querySelector('#loginBlock');
const step1Block = document.querySelector('#step1Block');
const regBlock = document.querySelector('#regBlock');
const regBtn = document.querySelector('#regBtn');
const toLoginBtn = document.querySelector('#toLoginSvg');
const toStep2Btn = document.querySelector('#toStep2Btn');
const from3to2Btn = document.querySelector('#from3to2Svg');
const createAccountBtn = document.querySelector('#createAccount');
const inpName = document.querySelector('#name');
const inpEmail = document.querySelector('#email');
const inpPassword = document.querySelector('#password');
const inpPasswordNext = document.querySelector('#password_next');
const form = document.querySelector('.form-login');
const person = {};

regBtn.addEventListener('click', (event) => {
    loginBlock.style.display = 'none';
    step1Block.style.display = 'flex';
});
toLoginBtn.addEventListener('click', (event) => {
    loginBlock.style.display = 'flex';
    step1Block.style.display = 'none';
});
from3to2Btn.addEventListener('click', (event) => {
    regBlock.style.display = 'none';
    step1Block.style.display = 'flex';
});

toStep2Btn.addEventListener('click', (event) => {
    step1Block.style.display = 'none';
    regBlock.style.display = 'flex';

    const inp = document.querySelector('form input:checked');
    if (inp.id === 'user_student') {
        person.type = 'student';
    } else {
        person.type = 'teacher';
    }

});


createAccountBtn.addEventListener('click', (event) => {

    const [name, surName] = inpName.value.split(' ');
    if (!name || name.length < 2 || !surName || surName.length < 2) {
        inpName.classList.add('error');
        console.error('введіть правильне імя та прізвище');
        return;

    }
    if (inpPassword.value !== inpPasswordNext.value) {
        inpPassword.classList.add('error');
        inpPasswordNext.classList.add('error');
        console.error('паролі мають бути однаковими');
        return;
    }


    person.name = inpName.value,
        person.email = inpEmail.value,
        person.password = inpPassword.value;

    const key = 'login';
    localStorage.setItem(key, JSON.stringify(person));

    if (person.type === 'student') {
        const key = 'student';

        if (localStorage.getItem(key)) {
            const dataFromLS = localStorage.getItem(key);
            const parsedData = JSON.parse(dataFromLS);
            parsedData.push(person);
            localStorage.setItem(key, JSON.stringify(parsedData));
        } else {
            localStorage.setItem(key, JSON.stringify([person]));
        }
        window.location.href = 'student.html';
    } else {
        const key = 'teacher';
        localStorage.setItem(key, JSON.stringify(person));
        window.location.href = 'teacher.html';
    }

    querySelector('.form-login').reset();
});
console.log('wizard');